"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import { Menu, X } from "lucide-react";
import { motion, AnimatePresence } from "framer-motion";

export function Navbar() {
  const [isOpen, setIsOpen] = useState(false);
  const [scrollY, setScrollY] = useState(0);
  const [isDark, setIsDark] = useState(true);

  useEffect(() => {
    const handleScroll = () => {
      const currentScrollY = window.scrollY;
      setScrollY(currentScrollY);

      // Determine if navbar should be light or dark based on scroll position
      // Adjust these values based on your section heights
      const heroEnd = 800; // End of Hero section
      const whatWeDoEnd = 1600; // End of WhatWeDo section
      const whyChooseUsEnd = 2400; // End of WhyChooseUs section

      if (currentScrollY < heroEnd) {
        setIsDark(true); // Hero is dark
      } else if (currentScrollY >= heroEnd && currentScrollY < whatWeDoEnd) {
        setIsDark(false); // WhatWeDo is light
      } else if (currentScrollY >= whatWeDoEnd && currentScrollY < whyChooseUsEnd) {
        setIsDark(false); // WhyChooseUs is light
      } else {
        setIsDark(true); // CTA section is dark
      }
    };

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  const navLinks = [
    { name: "Home", href: "/" },
    { name: "Services", href: "/services" },
    { name: "About", href: "/about" },
    { name: "Contact", href: "/contact" },
  ];

  return (
    <nav
      className="fixed top-0 left-0 right-0 z-50 transition-all duration-300"
      style={{
        backgroundColor: scrollY > 50 
          ? (isDark ? "rgba(15, 23, 42, 0.9)" : "rgba(255, 255, 255, 0.9)")
          : "transparent",
        backdropFilter: scrollY > 50 ? "blur(10px)" : "none",
      }}
    >
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center h-16">
          {/* Logo */}
          <Link href="/">
            <motion.div
              whileHover={{ scale: 1.05 }}
              className="text-2xl font-bold"
              style={{
                color: isDark ? "#10B981" : "#0F172A",
              }}
            >
              Growth Supercharged
            </motion.div>
          </Link>

          {/* Desktop Navigation */}
          <div className="hidden md:flex items-center space-x-8">
            {navLinks.map((link) => (
              <Link key={link.name} href={link.href}>
                <motion.span
                  whileHover={{ scale: 1.1 }}
                  className="font-medium transition-colors"
                  style={{
                    color: isDark ? "#FFFFFF" : "#0F172A",
                  }}
                >
                  {link.name}
                </motion.span>
              </Link>
            ))}
            <Link href="/contact">
              <motion.button
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
                className="px-6 py-2 rounded-lg font-semibold transition-all"
                style={{
                  backgroundColor: isDark ? "#10B981" : "#0F172A",
                  color: "#FFFFFF",
                }}
              >
                Get Started
              </motion.button>
            </Link>
          </div>

          {/* Mobile Menu Button */}
          <button
            onClick={() => setIsOpen(!isOpen)}
            className="md:hidden"
            style={{
              color: isDark ? "#FFFFFF" : "#0F172A",
            }}
          >
            {isOpen ? <X size={24} /> : <Menu size={24} />}
          </button>
        </div>
      </div>

      {/* Mobile Navigation */}
      <AnimatePresence>
        {isOpen && (
          <motion.div
            initial={{ opacity: 0, height: 0 }}
            animate={{ opacity: 1, height: "auto" }}
            exit={{ opacity: 0, height: 0 }}
            className="md:hidden"
            style={{
              backgroundColor: isDark ? "#0F172A" : "#FFFFFF",
            }}
          >
            <div className="px-4 pt-2 pb-4 space-y-3">
              {navLinks.map((link) => (
                <Link key={link.name} href={link.href}>
                  <div
                    className="block py-2 font-medium"
                    onClick={() => setIsOpen(false)}
                    style={{
                      color: isDark ? "#FFFFFF" : "#0F172A",
                    }}
                  >
                    {link.name}
                  </div>
                </Link>
              ))}
              <Link href="/contact">
                <button
                  className="w-full px-6 py-2 rounded-lg font-semibold"
                  onClick={() => setIsOpen(false)}
                  style={{
                    backgroundColor: isDark ? "#10B981" : "#0F172A",
                    color: "#FFFFFF",
                  }}
                >
                  Get Started
                </button>
              </Link>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </nav>
  );
}
